import React from 'react';
import { AudioPlayer, ContactBox, MainContentBox } from '../../components';
import { HomeDescription,StyledHeader, Wrapper} from './styled';

export const Home = () => {
  return (
    <MainContentBox>
      <Wrapper><MainContentBox $modified><h1>SaturdayLive - zesp muzyczny</h1></MainContentBox></Wrapper>
      <StyledHeader>
      
      </StyledHeader>
      
      
      <AudioPlayer />
      <h2>Imprezy firmowe</h2>
      <HomeDescription>
      <p>W ofercie na imprezy firmowe proponujemy Pastwu zesp w&nbsp;skadzie szecioosobowym (wokalistka, wokalista,gitarzysta, klawiszowiec, basista i&nbsp;perkusista). Rozkrcimy kad imprez i&nbsp;rozgrzejemy kady parkiet!!! Szeroki repertuar muzyki z lat 60-tych, 70-tych, 80-tych oraz najnowsze hity klubowe oraz radiowe za kadym razem podbijaj serca nawet najbardziej wymagajcych goci. Mamy dowiadczenie w realizacji takich imprez i potrafimy szybko&nbsp;i sprawnie wsppracowa&nbsp;z project menederami oraz ekipami nagonieniowymi nawet przy najbardziej napitych planach&nbsp;i harmonogramach. Nasz kierownik muzyczny bdzie do Pastwa dyspozycji aby ustali szczegy scenariusza, elementy techniczne oraz repertuar skrojony na miar tak, aby kady z&nbsp;Pastwa goci natychmiast ruszy do taca. W razie potrzeby chtnie polecimy firm dwikow ktra zna nasz rider, oraz DJ'a ktry mgby uzupeni ofert zespou.</p>
      </HomeDescription>

      <h2>Wesela</h2>
      <HomeDescription>
      <p>W ofercie na wesele proponujemy zesp szecioosobowy (wokalistka, wokalista,gitarzysta, klawiszowiec, basista, perkusista). Zapewniamy rwnie dj-a oraz obslug przez firm dwikowo-owietleniow. Dziki przekrojowemu repertuarowi i&nbsp;muzykom z&nbsp;najwyej pki jestemy w stanie dostosowa list wykonywanych utworw do wymaga klientw o&nbsp;rnych gustach i&nbsp;upodobaniach muzycznych. Gramy koncertowo w&nbsp;45-minutowych blokach muzycznych. Nasz DJ uzupenia wystp zespou zapewnic delikatne to muzyczne do kolacji, a po wystpie zespou na ywo porwie najbardziej wytrwaych goci do taca swoim tanecznym DJ-setem. Nad opraw wietlno-dwikow, przez cae przyjcie czuwa realizator dwiku,&nbsp; Prowadzimy imprez oraz ciekawe zabawy w&nbsp;dobrym stylu. Bez disco-polo i&nbsp;biesiady. Nasz kierownik zespou zadba o&nbsp;kady szczeg wystpu, bdzie do Pastwa dyspozycji w&nbsp;sprawach repertuarowych i&nbsp;w&nbsp;sprawie ustalenia szczegw imprezy. Biegle mwimy po angielsku i&nbsp;srazie potrzeby  poprowadzi wesele rwnie w&nbsp;tym w jzyku.</p>
      </HomeDescription>
      <ContactBox />
    </MainContentBox>
  );
};
